
@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Karla");
/*! normalize.css v3.0.3 | MIT License | github.com/necolas/normalize.css */
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
* {
  box-sizing: border-box;
}
html, body {
  height: 100.1%;
}
#app {
  font-family: 'Karla', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  margin: 0 auto;
}
body, p {
  font-family: 'Karla', Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.25em;
  font-weight: lighter;
  color: #696969;
  padding: 0;
  margin: 0;
}
p {
  margin-top: 0;
  margin-bottom: 1.35em;
}
p.intro {
    font-size: 20px;
}
@media only screen and (min-width: 992px) {
p.intro {
        max-width: 80%;
        margin-bottom: 0;
}
}
a, a:hover, a:visited, a:active {
  color: #2d9bd6;
  text-decoration: none;
}
a.major {
  position: relative;
  display: inline-block;
}
a.major:after {
    content: '';
    position: absolute;
    left: 1px;
    bottom: -1px;
    width: 0;
    height: .1em;
    background-color: #2d9bd6;
    background-color: rgba(45, 155, 214, 0.95);
    transition: .2s;
}
a.major:hover:after {
    width: 100%;
}
.wrapper {
  width: 85%;
  margin: 0 auto;
}
@media only screen and (min-width: 992px) {
.wrapper {
      width: 100%;
      max-width: 960px;
}
}
.featured-image {
  margin-top: 2em;
}
.page_title {
  font-size: 1em;
  font-weight: bold;
  text-transform: lowercase;
  letter-spacing: .01em;
  margin-bottom: 3em;
}
h2 {
  letter-spacing: -.02em;
  margin-bottom: .5em;
  font-size: 1.4em;
  line-height: 1em;
  margin-top: 1.3em;
}
@media only screen and (min-width: 992px) {
h2 {
      font-size: 2em;
      font-weight: 400;
}
}
h2.intro {
    margin: 2em 0;
}
@media only screen and (min-width: 992px) {
h2.intro {
        max-width: 80%;
        font-size: 2em;
}
}
h3 {
  font-weight: 400;
  font-size: 1.2em;
  margin-bottom: .2em;
  margin-top: 2em;
  line-height: 1.2em;
  color: #696969;
}
@media only screen and (min-width: 992px) {
h3 {
      font-size: 1.5em;
}
}
.section-heading {
  margin-bottom: 2em;
}
@media only screen and (min-width: 992px) {
.section-heading {
      max-width: 80%;
}
}
@media only screen and (min-width: 992px) {
.body {
    max-width: 65%;
}
}
footer {
  border-top: 4px solid #eee;
  margin-top: 5em;
  margin-bottom: 2em;
}
footer h3 {
    margin-bottom: 0;
    font-size: 28px;
}
footer p {
    margin-bottom: .35em;
    line-height: 1.45;
}
footer p a {
      color: #696969;
}
footer .copyright {
    margin-top: 2em;
    margin-bottom: 2em;
    font-size: 12px;
    color: #aaaaaa;
}
img {
  max-width: 100%;
  height: auto;
}
img.feature, img.featured-image {
    width: 100%;
    height: auto;
}
img.featured-image {
    margin-top: 2em;
}
.inline-list {
  display: inline-block;
  list-style-type: none;
  margin-left: 0;
  padding-left: 0;
  margin-top: 0;
}
.inline-list li {
    display: inline;
    margin-right: .5em;
}
.inline-list li:last-child {
      margin-right: 0;
}
.icon-list {
  padding-top: 4px;
  padding-left: 0;
}
.icon-list .icon {
    width: 24px;
    height: auto;
    opacity: .4;
    transition: opacity .2s;
}
.icon-list .icon:hover {
      opacity: .6;
}
.slat {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  margin: 2em 0;
}
.slat h3 {
    margin-top: 0;
}
.slat .column-half {
    margin-bottom: 1em;
}
@media only screen and (min-width: 992px) {
.slat {
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-direction: row;
          flex-direction: row;
      margin: 4.5em 0;
}
.slat.compressed {
        margin: 1em 0;
}
.slat .column-half {
        width: 50%;
        padding-right: 2em;
}
.slat .column-half:last-child {
          padding-right: 0;
}
.slat .column-two-thirds {
        width: 60%;
}
.slat .column-three-quarters {
        width: 75%;
}
.slat .column-quarter {
        width: 25%;
}
}
.row {
  display: -ms-flexbox;
  display: flex;
  margin: 2em 0;
  -ms-flex-direction: column;
      flex-direction: column;
}
@media only screen and (min-width: 992px) {
.row {
      margin: 5em 0;
      -ms-flex-direction: row;
          flex-direction: row;
}
.row.compressed {
        margin: 1em 0;
}
.row .column-quarter {
        width: 25%;
}
}
.row h3 {
    margin-top: 0;
}
.flex-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.flex-grid .column-third {
    text-align: center;
    margin-bottom: 1em;
    height: auto;
}
@media only screen and (min-width: 992px) {
.flex-grid .column-third {
        width: 28%;
}
}
.flex-grid .column-quarter {
    text-align: center;
    margin-bottom: 1em;
    height: auto;
}
@media only screen and (min-width: 992px) {
.flex-grid .column-quarter {
        width: 22%;
}
}
.text-center {
  text-align: center;
}
ul.dash-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
ul.dash-list > li {
    padding-left: 1em;
    margin-bottom: .5em;
}
ul.dash-list > li:before {
      content: "—";
      margin: 0 .25em 0 -1em;
      color: #2d9bd6;
      font-weight: bold;
}
.border {
  border: 1px solid #eee;
}
.drop-shadow {
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.25);
}



