
.logo {
  display: inline-block;
  margin: 2em 0 0;
  width: 140px;
  height: auto;
  transition: .5s;
}
.site-header {
  position: relative;
  width: 85%;
  max-width: 960px;
  margin: 0 auto;
}
@media only screen and (min-width: 992px) {
.site-header {
      width: 100%;
}
}
.toggle {
  position: fixed;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  color: white;
  background-color: #2d9bd6;
  border: 0;
  z-index: 9100;
  transition: .3s;
}
@media only screen and (min-width: 992px) {
.toggle {
      right: -100%;
}
.scrolled .toggle {
        right: 0;
}
}
.tcon {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  cursor: pointer;
  transition: .3s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
.tcon > * {
    display: block;
}
.tcon:hover, .tcon:focus {
    outline: none;
}
.tcon::-moz-focus-inner {
    border: 0;
}
.tcon-plus::before, .tcon-plus::after {
  content: "";
  border-radius: 1px;
  display: block;
  width: 60%;
  height: 10%;
  position: absolute;
  top: 44%;
  left: 20%;
  transition: .3s;
  background: white;
}
.tcon-plus--minus.tcon-transform::before {
  transform: rotate(45deg) translate(0, 0);
}
.tcon-plus--minus.tcon-transform::after {
  transform: rotate(135deg) translate(0, 0);
}
.tcon-visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.tcon-visuallyhidden:active, .tcon-visuallyhidden:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}
@media only screen and (min-width: 992px) {
.navbar {
    position: absolute;
    top: 0;
    right: 0;
    width: 70%;
}
}
.navbar ul {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media only screen and (min-width: 992px) {
.navbar ul {
      display: block;
      text-align: right;
}
.scrolled .navbar ul {
        display: none;
}
}
.navbar li {
  display: inline-block;
  text-align: left;
  padding: 0 16px;
  margin: 0;
  letter-spacing: .05em;
}
@media only screen and (min-width: 992px) {
.navbar li {
      font-size: 14px;
}
}
.navbar li a {
    display: inline-block;
    text-decoration: none;
    font-weight: bold;
    color: #696969;
    transition: color .2s;
    padding: 30px 0;
}
.navbar li a:hover {
      color: #2d9bd6;
}
@media only screen and (min-width: 992px) {
.navbar li a.router-link-exact-active {
        color: #2d9bd6;
}
}
.navbar.flyout ul {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding: 2em;
  background: #2d9bd6;
  text-align: left;
  z-index: 9000;
}
@media only screen and (min-width: 992px) {
.navbar.flyout ul {
      width: 50%;
}
}
.navbar.flyout ul li {
    display: block;
}
.navbar.flyout ul li a {
      padding: .5em 0;
      font-size: 2.5em;
      color: white;
      font-weight: bold;
      letter-spacing: 0;
}
@media only screen and (min-width: 768px) {
.navbar.flyout ul li a {
          padding: .5em 1em;
          font-size: 3em;
          letter-spacing: .05em;
}
}
.navbar #bar {
  display: none;
}
@media only screen and (min-width: 992px) {
.navbar #bar {
      display: block;
      width: 20px;
      height: 3px;
      background: #2d9bd6;
      position: absolute;
      bottom: 12px;
      left: 0;
}
}



